<template>
<div class="fx-question">
    <!-- <header-text :headerText="headerTitle" @header-event="onHeaderBtn" class="fx-header"></header-text> -->
    <div class="fx-tips">请如实回答下列问题：</div>
    <div class="fx-content" :style="{height:clientHeight+'px'}">
        <div class="wrapper">
            <div class="questionItems" v-show="vals.hasQ" v-for="(vals,i) in QUES_ILLS" :key="i">
                <question-tree :model="p" parentActive="-1" v-for="(p,m) in vals.children" :key="m"></question-tree>
            </div>
        </div>
    </div>
    <div class="fx-btns">
        <div class="preBtn" @click="goPrev()">上一步</div>
        <div class="nextBtn" @click="goNext()">确定</div>
    </div>
</div>
</template>
    
<script>
import Vue from "vue";
import headerText from "@/components/header";
import questionTree from "@/components/questionTree/questionTree";
export default {
    name:'question',
    data(){
        return{
            headerTitle:'智能核保',
            clientHeight:0,
            QUES_ILLS:[]
        }
    },
    created() {

    },
    methods: {
        //
        onHeaderBtn(){},
        //确定
        goNext(){
            let QUES_ILLS = JSON.parse(sessionStorage.getItem("TMP_QUES_ILLS"));
            this.hasMoreQueation='';
            this.checkMoreQuestion(QUES_ILLS);
            if(!this.hasMoreQueation){
                this.$router.push({path:'/fuxing/issue'})
            }
        },
        //上一步
        goPrev(){
            this.$router.go(-1);
            //this.$router.push({path:'/fuxing/index'});
        },
        // 校验是否还有未选择的问题
        checkMoreQuestion(data){
            if(this.hasMoreQueation)return;
            data.forEach((item,i)=>{
                if(item.type==2 && item.choosed==2){
                    this.hasMoreQueation=item.content;
                    this.$popup.alert('问题--"'+item.content+'"尚未选择，请您选择后再提交！');
                    return false;
                }
                if(item.children && item.children.length>0)this.checkMoreQuestion(item.children);
            })
        },
        checkHasQuestion(data){
            data.forEach((item,i)=>{
                item.hasQ=false;
                item.children.forEach((p,n)=>{
                    if(p.children && p.children.length>0){
                        item.hasQ=true;
                        return false;
                    }
                })
            })
        }
    },
    computed:{},
    components:{
        "header-text":headerText,
        "question-tree":questionTree
    },
    mounted(){
        let QUES_ILLS = JSON.parse(sessionStorage.getItem("TMP_QUES_ILLS"));
        this.checkHasQuestion(QUES_ILLS);
        this.QUES_ILLS = QUES_ILLS;
        sessionStorage.setItem("TMP_QUES_ILLS",JSON.stringify(QUES_ILLS));
        
        this.clientHeight = window.innerHeight
        -document.getElementsByClassName("fx-btns")[0].clientHeight
        -document.getElementsByClassName("fx-tips")[0].clientHeight;
    }
}
</script>

<style lang="scss">
.fx-question{
    background:#fff;
    .fx-content{
        overflow: hidden;
        width:100%;
        .wrapper{
            height:100%;
            width:100%;
            overflow: auto;
            background: #eee;
            .questionSubItem{
                padding-left: r(27);
                background:#fff;
                padding-bottom:r(30);
                margin-bottom: r(15);
                .title{
                    // height:r(78);
                    line-height:r(30);
                    padding-top:r(20);
                    padding-bottom:r(20);
                    font-size:r(23);
                    color:#666;
                }
                .subTitle{
                    font-size: r(28);
                    color:#000;
                    line-height:r(42);
                }
                .btns{
                    margin: 0 auto;
                    margin-top:r(18);
                    font-size: r(26);
                    overflow: hidden;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                }
            }
        }
    }
    .fx-tips{
        height:r(88);
        line-height:r(88);
        font-size:r(30);
        border-bottom:1px solid #dddddd;
        color:#4580f6;
        font-weight: bold;
        padding-left: r(27);
    }
    .fx-btns{
        height:r(92);
        position:fixed;
        bottom:0;
        width:r(750);
        border-top:1px solid #ddd;
        div{
            width:50%;
            height:100%;
            float:left;
            text-align: center;
            font-size:r(30);
        }
        .preBtn{
            color:#000;
            background:#fff;
            line-height:r(92);
        }
        .nextBtn{
            background:#0084ff;
            color:#fff;
            line-height:r(92);
        }
    }
}
</style>


