<template>
	<div>
		<div v-if="model.type==2">
			<div class="questionItem">
				<span class="subTitle">{{model.content}}</span>
				<div class="btns fc">
					<div v-for="(btnData, n) in model.children" :key="n">
						<span class="btn" :class="n==activeIndex?'active':''" @click="checkBtn(model.id,n)">{{btnData.content}}</span>
					</div>
				</div>	
			</div>
			<div v-if="model.children[activeIndex] && model.children[activeIndex].children && model.children[activeIndex].children.length>0">
				<question-tree :model="p" :parentActive="childActive" v-for="p in model.children[activeIndex].children" :key="p.id" ></question-tree>
			</div>
		</div>
		<div v-else>
			<div class="questionSubItem" v-if="model.children && model.children.length>0">
				<div class="title" v-if="model.children && model.children.length>0">- {{model.content}}</div>
				<div v-if="model.children && model.children.length>0">
					<question-tree :model="p" v-for="p in model.children" :key="p.id"></question-tree>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Vue from "vue";
import questionTree from "@/components/questionTree/questionTree";
	export default {
		name:"question-tree",
		props: {
			model: Object,
			parentActive: {
				// type: Number,
				default: -1
			}
		},
		components: {
			"question-tree": questionTree,
		},
		data() {
			console.log("this",this);
			return {
				activeIndex: -1,
				childActive: -1
			}
		},
		methods: {
			// 选择按钮，修改当前激活序列，修改atore中数据
			checkBtn(id, n) {
				this.activeIndex = n;
                this.childActive = this.childActive - 1; // 选择按钮时，重置子组件中的activeIndex
                var QUES_ILLS=JSON.parse(sessionStorage.getItem("TMP_QUES_ILLS"));
				this.eachList(QUES_ILLS,id,n);
				sessionStorage.setItem("TMP_QUES_ILLS",JSON.stringify(QUES_ILLS));
            },
            eachList(data,id,i){
                data.map(element => {
                    if(element.id==id){
                        element.choosed=1;
                        element.children.map((item,n)=>{
                            if(n==i){
                                item.choosed= 1;
                                if(item.children && item.children.length>0){
                                    item.children.map((c,s)=>{
                                        if(c.type==2)c.choosed=2;
                                    })
                                }
                            }else{
                                item.choosed= 0;
                                if(item.children && item.children.length>0){
                                    item.children.map((c,s)=>{
                                        if(c.type==2)c.choosed=0;
                                    })
                                }
                            }
                        })
                    }
                    if(element.children && element.children.length>0)this.eachList(element.children,id,i);
                });
            }
		},
		watch: {
			// 监听父组件的activeIndex修改，并重置当前组件的activeIndex；
			parentActive(newVal) {
				this.activeIndex = -1;
			},
		},
	};
</script>

<style lang="scss">
	.questionSubItem{
		margin-bottom: r(10);
		background: #fff;
	}
	.questionItem {
		background: #fff;

		.subTitle {
			width: r(710);
			font-size: r(30);
			color: #000;
			line-height: r(42);
			display: flex;
			flex-direction: row;
		}

		.btns {
			flex-wrap: wrap;
			margin-top: r(15);
			.btn {
                display:inline-block;
				min-width: r(210);
				padding: r(15) r(30);
                // height: 80rpx;
                font-size: r(30);
				text-align: center;
				// line-height: 76rpx;
				margin-right: r(30);
				border: r(2) solid #dddddd;
				border-radius: r(15);
				margin-bottom: r(15);
				&.active {
					background-color: #4580f6;
					color: #fff;
					border-color: #4580f6;
				}
			}
		}
	}
</style>



